        join(gpno){
            //o.add()
            //加入小组
            if(this.urid=='') alert('请先登录！');
            else {
                let params={gpno:gpno,username:this.urid};
                this.$http.post('/join.php',params).then(
                    response=>{
                        //console.log(response);
                        alert(response.data);
                    }
                )
            }
        }
    },
    created() {
        var guro=localStorage.getItem('username');
        var gurt=sessionStorage.getItem('username');
        if(guro!=null) this.urid=guro;
        if(gurt!=null) this.urid=gurt;
        // console.log(guro);
        // console.log(gurt);
        // console.log(this.urid);
        this.usrnm=this.urid;
        this.$http.post('/getgps.php').then(
            reponse=>{
                this.topic=[];
                var ss=reponse.data;
                var i;
                for(i=0;i<ss.length;i++){
                    this.topic.push({gpno:ss[i][0],name:ss[i][1],short:ss[i][2],url:ss[i][3]});
                }
            }
        )
    }
